// import { PinDropSharp } from '@material-ui/icons';
import React from 'react'
import {Link} from 'react-router-dom';


function CardPriceItem(props) {
    return (
        <>
           <li className="cards__item">
           <Link className="cards__item__link" to={props.path} >
                   <figure className="cards__item__pic-wrap" data-category={props.label} >
                      <img src={props.src} alt="Travel Images" className='cards__item__img' /> 
                   </figure>
                   <div className="cards__item__info">
                    <h5 className="cards__item__text">{props.text}</h5>
                     <ul>
                         <li>{props.price}</li>
                         <li>{props.people} poeple</li>
                         <li>Duration: {props.duration} days</li>
                         <li><span className="cards__span1">{props.revNumber} </span><span className="cards__span2">{props.revLeter}</span></li>
                         <li>{props.airport}</li>
                    </ul>
                    <button  to={props.path}  className="cards__item__button">Book It</button>
                   </div>
               </Link>
              

           </li> 
        </>
    )
}

export default CardPriceItem



