import React from 'react'
// import CardPriceItem from './CardPriceItem'
import './Card.css'
import img1 from '../images/img-ad-1.jpg'
import img2 from '../images/img-ad-2.jpg'
import img3 from '../images/img-ad-3.jpg'
import img4 from '../images/img-ad-4.jpg'
import img5 from '../images/img-ad-5.jpg'
import img6 from '../images/img-ad-6.jpg'
import CardPriceItem from './CardPriceItem'

const CardPrice = (props) => {
    return (
        <div className='cards'>
            <h1>Check out these EPIC Destinations!</h1>
            <div className="cards__container">
                 <div className="cards__wrapper">
                    <ul className="cards__items">
                        <CardPriceItem 
                        src={img1}
                        text='Pen y Fan and Corn Du (Wales)'
                        label='Adventure'
                        price='£ 3.300'
                        people='3'
                        duration='5'
                        revNumber='4.2 '
                        revLeter='Very Good'
                         airport='Heathrow'
                        />
                        <CardPriceItem
                        src={img2}
                        text='the Swiss Alps'
                        label='Adventure'
                        // path='/luxury'
                        price='£ 4.000'
                        people='2'
                        duration='5'
                        revNumber='4.8'
                        revLeter='Exelent'
                        airport='Paris'
                        />
                        <CardPriceItem
                        src={img3}
                        text='The Andorra Mountain '
                        label='Adventure'
                        price='£ 3.000'
                        people='3'
                        duration='6'
                        revNumber='4.3 '
                        revLeter='Very Good'
                        airport='Madrid'
                        />
                    </ul>
                    <ul className='cards__items'>
                        <CardPriceItem
                        src={img4}
                        text='The German Black Forest.'
                        label='Adventure'
                        price='£ 2.500'
                        people='4'
                        duration='3'
                        revNumber='4.2 '
                        revLeter='Very Good'
                        airport='Gatwick'                        
                        // path='/mistery'
                        />
                        <CardPriceItem
                        src={img5}
                        text='Himilayan Mountains'
                        label='Adventure'
                        price='£ 3.000'
                        people='3'
                        duration='6'
                        revNumber='4.3 '
                        revLeter='Very Good'
                        airport='Madrid'  
                        // path='/adventure'
                        />
                        <CardPriceItem
                        src={img6}
                        text='Ride through the Sahara Desert '
                        label='Adventure'
                        price='£ 3.000'
                        people='3'
                        duration='6'
                        revNumber='4.7'
                        revLeter='Exelent'
                        airport='Madrid'
                        // path='/adrenaline'
                        />
                    </ul>
                </div>
               
            </div>
        </div>
    )
}

export default CardPrice

