import React from 'react';
import Navbar from './components/Navbar';
import './App.css';
import Home from './components/pages/Home';
import { HashRouter as Router, Switch, Route } from 'react-router-dom';
import Services from './components/pages/Services';
import Products from './components/pages/Products';
import SignUp from './components/pages/SignUp';
import Adventure from './components/pages/Adventure';
import Luxury from './components/pages/Luxury';
import Mistery from './components/pages/Mistery';
import Adrenaline from './components/pages/Adrenaline';
import Footer from './components/Footer';

function App() {
  return (
    <>
      <Router>
        <Navbar />
        <Switch>
          <Route path='/' exact component={Home} />
          <Route path='/services' component={Services} />
          <Route path='/luxury' component={Luxury} />
          <Route path='/mistery' component={Mistery} />
          <Route path='/adrenaline' component={Adrenaline} />
          <Route path='/adventure' component={Adventure} />
          <Route path='/products' component={Products} />
          <Route path='/sign-up' component={SignUp} />
        </Switch>
        <Footer />
      </Router>
    </>
  );
}


export default App;
